html, body, #root {
  height: 100%;
}

body {
    padding: 0;
    margin: 0;
}

.container {
  min-height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1; 
  -ms-flex-direction: column; 
      flex-direction: column;
  background: #1e1e1e;
}

.inner {
  border: 1px solid white;
  padding: 10px;
  margin: 10px;

  -ms-flex: 1 1;

      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

@media only screen and (max-width: 600px) {
  .inner {
    border: none;
  }
}



